import React from "react";
const Input = ({ type, name, label, value, onChange, error }) => {
  return (
    <React.Fragment>
      <input
        id={name}
        name={name}
        type={type}
        placeholder={label}
        value={value}
        onChange={onChange}
        className="form-control contactform-input"
      />
      {error && (
        <div
          className="alert alert-danger pt-0 pb-0 ps-3 pe-0 mb-0"
          style={{ border: "0px", backgroundColor: "transparent" }}
        >
          {error}
        </div>
      )}
    </React.Fragment>
  );
};

export default Input;
